@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Topcontent{
    width:100%;
    overflow-x: hidden;
    min-height:100vh;
}
.pravin{
    margin-top: 90px;
    height:80vh;
}
.info{
    margin-top: 20%;
    left: 20%;
    position: relative;
}
.sm{
    width:  50px;
    height: 50px;
}
h5>span{
    color: orangered;
    font-size: 40px;
    font-family: 'Cormorant Garamond', serif;
}
.text{
    font-size: 25px;
    font-family: 'Cormorant Garamond', serif;
}
@media screen and (max-width: 600px){
    .info{
        top: 10%;
        left: 0%;
        position: relative;
    }
    .pravin{
        margin-top: 30px;
        height: 50vh;
    }
    h5>span{
        color: orangered;
        font-size: 25px;
        font-family: 'Cormorant Garamond', serif;
    }
    .text{
        font-size: 18px;
        font-family: 'Cormorant Garamond', serif;
    }
    .Topcontent{
        padding: 10px;
    }
}
.Mail{
   margin-top: 10%;
   left: 25%;
   right: 25%;
   width: 50%;
   position: relative;
   border: 2px solid black;
   border-radius: 20px;
   padding: 10px;
   font-family: 'Cormorant Garamond', serif;
}
.form-control{
   min-width:50%;
   max-width:100%
}
.Mail > h3{
   text-align: center;
   color: orangered;
   font-style: bold;
}
.Mail > h3> button {
   float:right;
}
@media screen and (max-width: 600px){
   .Mail{
      margin-top: 30%;
      width: 80%;
      left: 10%;
      right: 10%;
      position: relative;
   }
}
.Bottomcontent{
    color:orangered;
    height: 05vh;
}
p{
    color:white;
}
.col{
    margin-top: 50px;
}
.cover{
    width: 100%;
    padding: 20px;
    height: 50vh;
    border:1px solid black;
}
.projectcontainer{
	font-family: 'Cormorant Garamond', serif;
	min-height: 50vh;
}
.project{
	color: orangered;
	font-size: xx-large;
	text-align: center;
	width: 100%;
}
.poster-img{
	width:600px;
	height: 300px;
	border:2px solid black;
}
.card-text{
	text-align: center;
	font-style: bold;
	font-size: 18px;
	font-family: 'Ibarra Real Nova', serif;
}
.flex-container {
  display: none;
}
.scroll{
  overflow: auto;
  white-space: nowrap;
}
.scroll::-webkit-scrollbar {
    display: none;
}
@media screen and (max-width: 600px){
	.poster-img{
		width: 35vh;
		height: 30vh;
	}
	.card-text{
		font-size: 14px;
	}
}
.move{
	width: 30px;
	height: 30px;
}

.skillcontainer{
    font-family: 'Cormorant Garamond', serif;
}
.highlights{
    color: orangered;
    font-size: xx-large;
}
.highlight2{
    color: white;
    font-size: x-large;
}
.snaplogic{
    width:50%;
    padding: 1%;
}
.skilltab{
    width: 100%;
    height: 15vh;
    border:1px solid black;
    border-radius: 30px;
    padding: 4%;
}
.skillcontainer{
    background-color: white;
    color:black;
    width:100%;
}
@media screen and (max-width: 600px){
    .skilltab{
        width: 100%;
        height:10vh;
        border:1px solid black;
        border-radius: 30px;
        padding: 7%;
    }
    .highlight2{
        color: white;
        font-size: large;
    }
}
.workscontainer{
    max-height: 90vh;
    font-family: 'Ibarra Real Nova', serif;
    font-style: bold;
}
.works > center >h2{
    color:orangered;
}
.work{
    color: orangered;
    font-size: xx-large;
    text-align: center;
    width: 100%;
}
span{
    color: orangered;
    font-size: large;
}
.work{
    float: left;
    position:inherit;
}
label{
    color:orangered;
    float: left;
}
.container__left{
    color:white;
    float:left;
    width:300px;
    font-family: 'Alata', sans-serif;
}
.container-fluid > h1 > span{
    color: orangered;
    font-family: 'Alata', sans-serif;
}
.header{
    width:100%;
    position:fixed;
    z-index: 1;
    border-bottom: 2px solid orangered;
}
.nav-item{
  padding-top:10px;
  width:150px;
  color:white;
  text-align:center;
  font-family: 'Josefin Sans', sans-serif;
}
.nav-item:hover{
    cursor: pointer; 
    transition-timing-function: ease-in-out;
    color:orangered;
    border-bottom: 2px solid orangered;
  }
.butt{
    border-radius: 10px;
    color:white;
    padding:10px;
    font-family: 'Alata', sans-serif;
    width:220px;
    text-align:center;
}
.butt:hover{
    background-color: orangered;
    cursor: pointer;
    color:white;
}
.navbar-toggler-icon{
    float:right;
}
.navbar-toggler{
    border: none;
    outline: none;
}
.container__right{
    margin-left: 100px;
}
@media screen and (max-width: 600px) {
    .nav-item {
      float: right;
      width: 100%;
    }
    .container__right{
        margin-left:0px;

    }
    .butt{
        float: right;
        width: 100%;
    }
}

